/**
** - iPhone
**/
@media only screen and (max-width: 720px){
  .jumbo, .App, body {
    margin-top: 20px;
    background-position: center;
    background-color: #F9F6B6;
  }
  .jumbo {
    -webkit-animation: 1s ease-out 0s 1 SlideUp;
            animation: 1s ease-out 0s 1 SlideUp;
  }
  .singleCard {
    margin-left: 25px;
    margin-right: -100px;
  }
  .nav {
    background-color: #FBF59B;
  }
  #cardOne, #cardThree {
    -webkit-animation: 1s ease-out 0s 1 slideLeft;
            animation: 1s ease-out 0s 1 slideLeft;
  }
  #cardTwo {
    -webkit-animation: 1s ease-out 0s 1 slideRight;
            animation: 1s ease-out 0s 1 slideRight;
  }
}
